import React from "react";
import { useEffect, useState } from "react";
import "uikit/dist/css/uikit-core.min.css";
import "./styles.css";
import { Link } from "react-router-dom";
import Moment from 'react-moment';




const FinishedGames = () => {
  const [users, setUsers] = useState([]);
  const [pageNumber, setPageNumber] = useState(1)


  const incrementPageNumber = () => {
    setPageNumber(pageNumber => pageNumber + 1);
    console.log(pageNumber)
  }


  const decrementPageNumber = () => {
    if (pageNumber <= 1) return;
    setPageNumber(pageNumber => pageNumber - 1);
  }


  useEffect(() => {
    fetch(
      `https://api.pandascore.co/matches/past?sort=&page=${pageNumber}&per_page=10&token=a1trG0pytDA2N0RXkJVlWqA6MOb2aY8ii9szwMze-OabnW9QPu0`)
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        setUsers(data);
        console.log(data);
        setPageNumber(pageNumber)
      });


  }, [pageNumber]);




  return (
    <div className="container">
    
      <h2> Finished Games </h2>
      {users.length > 0 && (
        <div className="columns is-multiline">
          {users.map((user) => (
            <div
              className="column is-half gamesBoxes"
              key={user.id}
              id={user.id} >
              <div className="inner">
                <div className="inner__box	">
                  <Link to={user.slug} className="h2link" id={user.slug}>
                   {user.name}
                  </Link>
                  <span className="is-pulled-right tag is-danger">
                    <Moment date={user.original_scheduled_at} format="DD/MM/YYYY" /> 
                     </span>

                  <span className="is-pulled-right tag is-danger">
                    <Moment fromNow>{user.scheduled_at}</Moment>  
                    </span>



                </div>
              </div>
            </div>
          ))}
        </div>
      )}
      <div className="ctrl-div">
        <button className="ctrl-butts" onClick={incrementPageNumber} >Next</button>
        <button className="ctrl-butts" onClick={decrementPageNumber}> Previous</button>

      </div>
    </div>
  );
};

export default FinishedGames;
